@import "../../assets/styles/variables";

.gameboard {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;

  &__row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
  }
  &__tile {
    height: 8rem;
    width: 8rem;
    border: 4px solid rgba(124, 124, 124, 0.199);
    font-size: 6rem;
    text-align: center;
    font-weight: bold;
    line-height: 6.5rem;
    display: flex;
    justify-content: center;
  }
  .--correct-place {
    background-color: $color-green;
  }

  .--correct-letter {
    background-color: $color-yellow;
  }

  .--incorrect-letter {
    background-color: $color-grey;
  }
}

